.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Times New Roman, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

h2, p {
  margin: 0;
}

.catalog {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --columns: 4;
  }
}

.card {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  font-size: 10px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__title:hover, .card:hover .card__title {
  color: #34568b;
}

.card__subtitle {
  color: #616070;
  margin: 0 0 17px;
}

.card__info {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__info-price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

